import { Component, OnInit } from '@angular/core';
import { $ } from 'protractor';

@Component({
  selector: 'app-graph',
  templateUrl: './graph.component.html',
  styleUrls: ['./graph.component.css']
})
export class GraphComponent implements OnInit {

  yearList1: yearSummary[] = [];
  yearList2: yearSummary[] = [];
  yearList3: yearSummary[] = [];
  initialAmount:number = 1000;
  yearlyContribution = 1000;
  interestReturn = 10;


  constructor() { }

  ngOnInit() {
    this.recalculate();
  }

  recalculate():void{
      console.log("click actuated");
      this.yearList1 = [];
      this.yearList2 = [];
      this.yearList3 = [];
      let numOfYears:number = 30;
      let money:number = this.initialAmount;
      for(let i = 1; i <= numOfYears; i++ )
      {
          let year:yearSummary = new yearSummary();
          year.yearNumber = i;
          money += money * (this.interestReturn / 100) + this.yearlyContribution;
          year.dollarAmount = '$' + Number(Math.round(money*100)/100).toLocaleString();
          if(i <= 10){
            this.yearList1.push(year);
          }
          else{
            (i <=20 ) ? this.yearList2.push(year):this.yearList3.push(year);
          }
      }
  }
}

class yearSummary{
    yearNumber = 0;
    dollarAmount = "";
}
